import React from 'react';
import './Analytics.sass'
import {connect} from "react-redux";


const Analytics = props => {
    const cls = []
    return (
        <div style={{width: '50%'}}>
            <section className="tableBlock">
                <ul className="table">
                    {props.statics.length > 0 ?
                        props.statics.map(obj => {
                            return <li key={obj.name + obj.id + Math.random()} className="table_item">
                                <div className="table_inf">
                                    <div className="table_icon">
                                         <i className="far fa-user"></i>
                                    </div>
                                    <h3>{obj.name}</h3>
                                    <h3>{obj.point}</h3>
                                    <h3 className={obj.result === 'Good' ? 'good' : 'bad'}>{obj.result}</h3>
                                </div>
                       </li>
                    })
                        :
                        <h1>Empty</h1>
                    }
                </ul>
            </section>
        </div>
    );
};


function mapStateToProps(state){
    return {
        statics: state.statics
    }
}

export default connect(mapStateToProps)(Analytics);
