import {ADD_TO_STATICS, REDRAW_STATE_ELEMENT} from "./actionsType/actionsType";
import {clearArr} from "./clearArr";

const initialState = {
    statics: [],
    limitStatics : [],
    drawToggle: false
}
const arr = [];
export function rootReducer(state = initialState, action){
   switch (action.type){
       case ADD_TO_STATICS:
           arr.push(action.payload);
           return {
               statics: [...arr],
               limitStatics: clearArr(arr)
           }

       case REDRAW_STATE_ELEMENT :
           let array = [...state.statics];
           array[action.payload.id - 1] = action.payload
           return {
               statics: array,
               limitStatics: clearArr(arr)
           }
       default : return state
   }
}







