import React, {Component} from 'react';
import Card from "../Ui/Card/Card";
import './Main.sass'
class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeCard:1,
            persons: [
                {
                    name:'David',
                    time:3000,
                    point:'Passport Review',
                    id:1,
                    valid: true,
                    isFinished:false
                },
                {
                    name:'Andrea',
                    time:4000,
                    point:'Check on',
                    id:2,
                    valid: true,
                    isFinished:false

                },
                {
                    point:'Practice',
                    name:'John',
                    time:5000,
                    id:3,
                    valid: true,
                    isFinished:false
                },
                {
                    point:'Practice',
                    name:'Pashkevich',
                    time:2000,
                    id:4,
                    valid: true,
                    isFinished:false
                },
                {
                    point:'Practice',
                    name:'Eric',
                    time:6000,
                    id:5,
                    valid: true,
                    isFinished:false
                },
                {
                    point:'Practice',
                    name:'Ilyusha',
                    time:2000,
                    id:6,
                    valid: true,
                    isFinished:false
                },
            ]
        }

    }



    renderCard = (arr) => {
        let timeCheck = []
        const reducer = (accumulator, currentValue) => accumulator + currentValue;

        return arr.map((el,index) => {
            timeCheck.push(el.time);
            return <Card time={el.time} key={el.id} timeToStart={timeCheck.reduce(reducer)} id={el.id} name={el.name} point={el.point}/>
        })
   }


    render() {
        return (
           <div>
               <div className={"Nav"}>
                   {
                       this.renderCard(this.state.persons)
                   }
               </div>
           </div>
        )
    }
}

export default Main;