import {ADD_TO_STATICS,REDRAW_STATE_ELEMENT} from "../actionsType/actionsType";

export function addToAnalytics(obj){
    return {
        type: ADD_TO_STATICS,
        payload: obj
    }
}

export function redrawFunction(obj){
    return{
        type: REDRAW_STATE_ELEMENT,
        payload: obj
    }
}
