import React from 'react'
import './App.css';
import Analytics from "./Component/Analytics/Analytics";
import Layout from './Component/Layout/Layout'
import Main from "./Component/Main/Main";
import Priority from "./Component/Priority/Priority";
import Backdraw from "./Component/Backdraw/Backdraw";
import {connect} from "react-redux";
import InfoCard from "./Component/Ui/InfoCard/InfoCard";

class App extends React.Component{
    state = {
        shadowToggle: false,
        activeCard: {}
    }

    toggleHandlerCard = (obj) => {
        this.setState({
            shadowToggle: !this.state.shadowToggle,
            activeCard: obj
        })

    }


  render() {
    return (
        <div className="App">
            <Layout>
                {this.state.shadowToggle ? <Backdraw onClickHandler={this.toggleHandlerCard}>
                    <InfoCard data={this.state.activeCard}/>
                </Backdraw> : null }
                    <Main/>
                    <hr/>
                    <div className="container">
                        <Analytics/>
                        <Priority onClickHandler={this.toggleHandlerCard} />
                    </div>
            </Layout>
        </div>
    );
  }
}

function mapStateToPropsApp(state){
    return{
        statics: state.statics
    }
}

export default connect(mapStateToPropsApp)(App);
