import React,{useState,useEffect} from 'react';
import './Card.sass'
import gsap from 'gsap'
import {addToAnalytics} from "../../../redux/actions/actions";
import {connect} from "react-redux";

const Card = props => {
    const [isFinished,setFinished] = useState(false);
    const [visible, setVisible] = useState(true);
    const [currentTime,setTime] = useState(props.time)
    const [intervalMarker,setIntervalMarker] = useState(false);

    useEffect(() => {
        const interval = setInterval(() => {
            console.log('popizdi')
            setTime(currentTime - 1000)
        }, 1000);
        return () => clearInterval(interval);
    }, []);


    let newObj = {
        id: props.id,
        name: props.name,
        point: props.point,
        timeToStart: props.timeToStart ? props.timeToStart : props.time,
        result: Math.floor(Math.random() * 10) >= 4 ? 'Good' : 'Bad'

    }



    if(visible === true){
        let timer = window.setTimeout(()=>{
            const tl = gsap.timeline({
                onComplete: () => {
                    props.addToStore(newObj)
                    setFinished(true)
                }
            })
            clearTimeout(timer)

            tl.to(`.card${props.id}`, .5, {y: -20, opacity: 0})
        },props.timeToStart)

        setVisible(false)
    }



    return (
            isFinished === true ? false :
            <div className={`card${props.id} card`} onClick={props.onFocus}>
                <div className="card_header">
                    <div className="card_options">
                        <div className="card_offer">
                            <h1 className="card_offer-title">{props.point}</h1>
                        </div>
                        <div className="card_time">
                            <h1>{currentTime}</h1>
                        </div>
                    </div>
                    <div className="card_number">
                        <p>{'â„– ' + props.id}</p>
                    </div>
                </div>
                <div className="card_main">
                    <div className="card_main-info">
                        <div className="card_main-info-img">
                            <i className="far fa-user"></i>
                            <div className="card_main_titles">
                                <h1>{props.name}</h1>
                            </div>
                        </div>
                        <div className="card-main-status">
                        </div>
                    </div>
                </div>
            </div>
    );
};

function mapStateToProps(state){
    return {
        statics: state.statics
    }
}

function mapDispatchToPropses(dispatch){
    return {
        addToStore: obj => dispatch(addToAnalytics(obj))
    }
}

export default connect(mapStateToProps,mapDispatchToPropses)(Card);
